.header {
  position: relative;
  height: 92px;

  & .hider__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    //   height: 72px;
    height: 92px;
    transition: height 250ms cubic-bezier(0.4, 0, 0.2, 1);
    display: flex;
    align-items: center;
    background-color: var(--bg-white);
    box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
  }

  & .header-nav__link {
    font-weight: 400;
    font-size: 15px;
    line-height: calc(16 / 14);
    color: var(--black);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &.current,
    &:hover,
    &:focus {
      color: var(--btn-green);
      text-decoration: underline;
    }

    @include desktop {
      color: var(--title-black);
    }
  }
  & .logo__icon {
    // width: 66px;
    // height: 52px;
    width: 80px;
    height: 62px;
  }
}
