.header-scroll {
  height: 72px;

  .hider__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    height: 72px;
    //   height: 100px;
    display: flex;
    align-items: center;
  }

  & .header-nav__link {
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16 / 14);
    color: var(--black);

    &.current,
    &:hover,
    &:focus {
      color: var(--btn-green);
      text-decoration: underline;
    }

    @include desktop {
      color: var(--title-black);
    }
  }
  & .logo__icon {
    width: 66px;
    height: 52px;
    // width: 80px;
    // height: 62px;
  }
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include tablet {
    justify-content: flex-start;
  }
}

.logo {
  display: block;

  @include tablet {
    margin-right: auto;
  }
}

