.mobile-menu {
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;

  &:active .mobile-menu__icon,
  &:hover .mobile-menu__icon,
  &:focus .mobile-menu__icon {
    fill: var(--yellow);
  }
}

.mobile-menu__icon {
  width: 28px;
  height: 28px;
  fill: var(--title-green);
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.menu-position {
  position: relative;

  @include tablet {
    display: none;
  }
}

.mobile-menu-container {
  position: absolute;
  width: 130px;
  right: 0;
  top: 28px;
  background-color: var(--bg-white);
  overflow: hidden;
  box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
  border-radius: 30px 0px 30px 30px;
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.mobile-menu-container.is-open {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.mobile-menu-container-button {
  position: absolute;
  top: 4px;
  right: 4px;
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
}

.mobile-menu-container__icon {
  width: 24px;
  height: 24px;
  fill: var(--title-black);
}

.mobile-menu__link {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(19 / 16);
  color: var(--black);

  display: block;
  padding: 16px;
  background-color: transparent;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: var(--btn-green);
  }
}

.header-nav {
  display: none;

  @include tablet {
    display: block;
    margin-right: 30px;
  }
}

.header-nav-list {
  display: flex;
  gap: 28px;
}

