.header-button {
  display: none;

  @include tablet {
    font-weight: 500;
    font-size: 13px;
    line-height: 1.5;
    color: var(--title-black);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 98px;
    height: 32px;

    background: var(--btn-green);
    border-radius: 30px;
    border: 0;
    &:hover,
    &:focus {
      background-color: var(--yellow);
    }
  }
}

